.About {
  position: relative;
  background-color: rgb(5 12 70);
  color: rgb(180, 211, 221);
  z-index: -1;
}
.About .container {
  opacity: 0%;
  transition: 0.5s;
  display: grid;
  padding: 80px 60px;
  grid-template-columns: repeat(3, 1fr);
}

.About .information {
  line-height: 1.75;
  padding: 190px 0px 0px 70px;
}
.About .aboutme h2 {
  color: blue;
  padding: 10px 0px 30px 30px;
  font-size: 32px;
}
.About .title {
  font-weight: bold;
}
.img-profile img {
  margin-left: 50px;
  width: 320px;
  padding-top: 10px;
}
.About li {
  list-style: none;
}

@media screen and (max-width: 1024px) {
  .About .container {
    grid-template-columns: 30% 35% 35%;
  }
  .About .aboutme h2 {
    padding: 10px 0px 30px 20px;
  }
  .img-profile img {
    margin-left: 0px;
    padding-top: 50px;
  }
  .About .information {
    padding-top: 250px;
  }
}

@media screen and (max-width: 768px) {
  .About .container {
    padding: 10px 15px;
    grid-template-areas:
      "data-right data-right"
      "img data-left";
    grid-template-columns: repeat(2, 50%);
    height: 80vh;
  }
  .About .aboutme {
    grid-area: data-right;
    padding-top: 50px;
  }
  .About .information {
    grid-area: data-left;
    padding: 150px 0px 0px 20px;
  }
  .img-profile img {
    grid-area: img;
    width: 300px;
    padding-top: 30px;
  }
  /* end style about */
}
@media screen and (max-width: 605px) {
  .About .container {
    padding: 15px 15px;
  }
  .About .information {
    padding: 120px 0px 0px 45px;
  }
}
@media screen and (max-width: 480px) {
  .About .information {
    padding: 20px 0px 0px 0px;
  }
  .About .container {
    transform: translateY(-30px);
    height: auto;
  }
  .img-profile img {
    transform: translateY(50px);
    width: 150px;
  }
}
