.Resume {
  position: relative;
  background-color: rgb(25, 0, 75);
  color: rgb(180, 211, 221);
  z-index: -1;
}
.Resume .container {
  transition: 0.5s;
  opacity: 0%;
  padding: 100px 50px;
  height: 100vh;
}
.Resume li {
  list-style: none;
  border-left: solid white 3px;
  padding-left: 15px;
}
.edu .time,
.exp .time {
  position: relative;
}
.edu .time::after,
.exp .time::after {
  position: absolute;
  width: 10px;
  height: 10px;
  left: -10px;
  bottom: 10px;
  content: "";
  background-color: rgb(5, 5, 175);
  border: 4px solid white;
  border-radius: 100%;
}

.Resume h2 {
  color: blue;
  font-size: 32px;
  padding: 15px 0px 0px 20px;
}
.Resume .Container-data {
  display: flex;
  margin: 40px 0px 0px 100px;
}
.Resume h3 {
  font-size: 24px;
  padding-bottom: 20px;
}
.Resume h4 {
  /* font-size: 21px; */
  padding-bottom: 10px;
}
.Resume h5 {
  padding-left: 15px;
  /* font-size: 13px; */
  line-height: 1.75;
}
.edu {
  margin-left: 60px;
}
.edu li:nth-child(2),
.edu li:nth-child(4) {
  padding-bottom: 80px;
}
.exp li:nth-child(even) {
  padding-bottom: 30px;
}
.skill {
  display: grid;
  margin: 30px 0px 0px 60px;
  grid-template-columns: 1fr 1fr 1fr;
}
.skill img {
  margin: 20px 15px;
  height: 50px;
}

/* start responsive  style */
@media screen and (max-width: 768px) {
  .skill {
    grid-template-columns: 1fr;
    margin: 30px 0px 0px 20px;
  }
  .skill .visible:nth-child(9) img {
    position: relative;
    bottom: 85px;
    right: 100px;
  }
  .skill .visible:nth-child(10) img {
    position: relative;
    bottom: 180px;
    right: 200px;
  }
  .skill .visible:nth-child(11) img {
    position: relative;
    bottom: 270px;
    right: 300px;
  }
}
@media screen and (max-width: 605px) {
  .skill {
    margin: 30px 0px 0px 0px;
  }
  .Resume .Container-data {
    margin: 30px 0px 0px 10px;
  }
  .Resume h2 {
    padding: 20px 0px 30px 0px;
  }
  .Resume .container {
    padding: 50px 30px;
  }
}
@media screen and (max-width: 480px) {
  .skill {
    margin: 30px 0px 0px 0px;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .skill .visible:nth-child(9) img,
  .skill .visible:nth-child(10) img,
  .skill .visible:nth-child(11) img {
    bottom: 0px;
    right: 0px;
  }
  .Resume .container {
    height: auto;
  }
  .Resume .Container-data {
    display: block;
    margin: 30px 0px 0px 0px;
  }
  .edu {
    margin: 50px 0px 0px 5px;
  }
}
@media screen and (max-width: 375px) {
  .Resume .Container-data {
    grid-template-columns: 35% 60%;
  }
}
