* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
nav {
  position: fixed;
  width: 100vw;
  background-color: rgb(0, 0, 95);
  border-bottom: solid 2px white;
  z-index: 2;
}
nav ul {
  margin-right: 55px;
  display: flex;
  justify-content: flex-end;
}
nav .menu {
  display: flex;
}
.bar i {
  visibility: hidden;
  padding-top: 20px;
  display: block;
  text-align: left;
}
nav li {
  font-weight: bold;
  /* font-size: 16px; */
  text-align: center;
  height: 35px;
  list-style: none;
  margin: 20px 30px;
}
nav li a:hover {
  color: blue !important;
}

nav li a {
  color: rgb(213, 224, 228);
  text-decoration: none;
  transition: 0.5s;
}
@media screen and (max-width: 768px) {
  nav .meun {
    margin-right: 0px;
  }
}
@media screen and (max-width: 605px) {
  nav {
    background-color: transparent;
    width: auto;
    border-bottom: none;
  }
  nav ul {
    margin-right: 0px;
    flex-direction: column;
  }
  nav .menu {
    background-color: rgba(20, 59, 180, 0.7);
    flex-direction: column;
    border-bottom-right-radius: 30px;
  }
  nav li {
    margin: 10px 0px 50px 0px;
  }
  nav .bar {
    border-top-right-radius: 20px;
    width: 100px;
    height: 80px;
    background-color: rgb(0, 0, 95);
  }
  nav .bar i {
    padding: 20px 0px 0px 40px;
    color: red;
    visibility: visible;
  }
}
