@import "~react-image-gallery/styles/css/image-gallery.css";
.Portfoloi {
  height: auto;
  background-color: rgb(20 10 85);
}
.Portfoloi .container {
  transition: 0.5s;
  opacity: 0%;
}
.Portfoloi .container-in {
  margin-bottom: 10px;
  border-radius: 30px;
  transition: 0.2s;
}

.Portfoloi .projectlist {
  width: 930px;
  display: flex;
  gap: 15px;
  margin: auto;
  overflow: auto;
  color: rgb(180, 211, 221);
}
.Portfoloi .projectlist::-webkit-scrollbar {
  height: 15px;
  border-radius: 10px;
  background-color: blue;
}
.Portfoloi .projectlist::-webkit-scrollbar-button {
  background-color: aquamarine;
  height: 20px;
  width: 20px;
}
.Portfoloi .projectlist::-webkit-scrollbar-thumb {
  background-color: red;
  border-radius: 10px;
}

.Portfoloi h2 {
  color: blue;
  padding: 90px 0px 20px 100px;
  font-size: 32px;
}
.Portfoloi .projectlist img {
  border-radius: 30px 30px 0px 0px;
}

.Portfoloi p {
  padding: 20px 15px;
  height: 270px;
  width: 300px;
  position: relative;
  text-indent: 50px;
  background-color: rgb(66, 31, 120);
  border-radius: 0px 0px 30px 30px;
}
.Portfoloi p a {
  position: relative;
  top: 20px;
  color: white;
  text-decoration: none;
}
.Portfoloi p a:hover {
  display: block;
  color: turquoise;
  transform: translateY(-5px);
}
.Portfoloi h3 {
  position: absolute;
  right: 25px;
  top: 185px;
}
.Portfoloi .logoskill {
  display: flex;
  position: absolute;
  right: 25px;
  top: 135px;
}
.logoskill img {
  margin: 3px;
  color: rgb(213, 224, 228);
  width: 40px;
}

/* start responsive  style */

@media screen and (max-width: 768px) {
  .Portfoloi .projectlist {
    width: 615px;
  }
}
@media screen and (max-width: 605px) {
  .Portfoloi .projectlist {
    width: 315px;
  }
  .Portfoloi h2 {
    color: blue;
    padding: 50px 0px 50px 50px;
    font-size: 32px;
  }
}
