#contact-effect {
  transition: 0.5s;
}
.Contact {
  position: relative;
  z-index: 1;
  height: auto;
  background-color: rgb(15, 20, 100);
}
.Contact .container {
  width: 750px;
  height: 300px;
  margin: auto 300px;
  display: flex;
  gap: 30px;
  color: rgb(180, 211, 221);
}
.Contact h2 {
  color: blue;
  font-size: 32px;
  padding: 35px 0px 0px 60px;
}

.social ul {
  display: flex;
  align-items: center;
}
.social li {
  margin: 20px;
  list-style: none;
}

.Contact form {
  display: grid;
  grid-template-areas:
    "name email"
    "message message";
  background-color: rebeccapurple;
  position: relative;
}
form input {
  background-color: transparent;
  width: 100%;
  height: 35px;
}
.Contact .name,
.Contact .email {
  height: 30%;
  width: 100%;
  padding: 20px 20px 0px 20px;
}
form .message {
  grid-area: message;
  padding: 0px 20px 20px 20px;
}
form .message input {
  width: 100%;
  height: 100px;
}
form label {
  display: block;
  padding-bottom: 20px;
}
.container iframe {
  width: 50%;
}
.Contact button {
  position: absolute;
  bottom: 0px;
  left: 160px;
  width: 80px;
  height: 30px;
}

/* end contact */

footer {
  margin-top: 50px;
  background-color: rgb(0, 60, 170);
}
footer li img {
  width: 35px;
  margin: auto 10px;
}
footer ul {
  display: grid;
  grid-template-columns: 5% 20% 5% 20% 5% 20%;
}
footer li {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 10px;
}
footer li h4 {
  color: rgb(213, 224, 228);
}
footer li h4:nth-child(1) {
  margin-bottom: 25px;
}

@media screen and (max-width: 1024px) {
  footer li img {
    margin: auto 0px;
  }

  footer ul {
    grid-template-columns: 5% 25% 5% 25% 5% 25%;
  }
  .Contact .container {
    margin: auto;
    transform: translateY(50px);
  }
  footer {
    margin-top: 150px;
    top: 210px;
  }
}

@media screen and (max-width: 768px) {
  .Contact .container {
    width: 600px;
    height: 300px;
  }
  .Contact button {
    left: 100px;
  }
  footer ul {
    grid-template-columns: 10% 30% 10% 50%;
  }
}
@media screen and (max-width: 605px) {
  .Contact .container {
    width: 550px;
    height: 270px;
  }
  .Contact button {
    left: 90px;
  }
}

@media screen and (max-width: 605px) {
  .skill {
    margin: 30px 0px 0px 0px;
  }
  .Resume .Container-data {
    margin: 30px 0px 0px 10px;
  }
}
@media screen and (max-width: 480px) {
  .Contact .container {
    width: 300px;
    display: flex;
    flex-direction: column;
    height: auto;
  }
  .container iframe {
    width: 100%;
    height: 250px;
  }
  .Contact button {
    left: 100px;
    bottom: 0px;
  }
  footer ul {
    grid-template-columns: 20% 80%;
  }
}
