.Home {
  position: relative;
  height: 100vh;
  background-color: rgb(0, 5, 75);
}
.data-profile {
  position: relative;
}
.Title {
  position: absolute;
  bottom: 100px;
  left: 450px;
  transition: 0.5s;
  color: rgb(180, 211, 221);
  margin-top: 100px;
  font-weight: bold;
}
.Title h1 {
  padding: 100px 0px 0px 100px;
}

.Title h2 {
  padding: 40px 50px 100px 250px;
}
.data-profile img {
  margin-left: 100px;
  margin-top: 100px;
  width: 340px;
  filter: drop-shadow(20px 0px 100px rgb(0, 25, 130));
}
/* start responsive  style */
@media screen and (max-width: 1024px) {
  .data-profile {
    top: 40px;
  }
  .data-profile img {
    width: 350px;
    margin-left: 50px;
  }
  .Title {
    top: 50px;
    left: 380px;
  }
  .Title h1 {
    padding: 150px 0px 0px 50px;
  }
  .Title h2 {
    padding: 60px 0px 0px 120px;
  }
}

@media screen and (max-width: 768px) {
  .data-profile img {
    transform: translateX(130px);
    margin-top: 100px;
  }
  .Title {
    left: 20px;
    top: 450px;
  }
  .Title h2 {
    transform: translateX(100px);
  }
}
@media screen and (max-width: 605px) {
  .data-profile img {
    transform: translateX(70px);
    margin-top: 80px;
  }
  .Title {
    left: 0px;
    top: 420px;
  }
  .Title h2 {
    transform: translateX(70px);
  }
}
@media screen and (max-width: 480px) {
  .data-profile img {
    transform: translateX(0px);
    margin-top: 100px;
    width: 300px;
  }
  .Title {
    left: -20px;
    top: 380px;
  }
  .Title h2 {
    transform: translateX(0px);
    padding: 20px 0px 0px 120px;
  }
}
@media screen and (max-width: 375px) {
  .data-profile img {
    width: 220px;
    transform: translateX(20px);
    margin-top: 50px;
  }
  .Title {
    top: 150px;
  }
  .Title h1,
  .Title h2 {
    transform: translateX(-25px);
  }
}
